import { useEffect, React } from "react";
import { useConfluxPortal, AccoutsModal } from 'use-confluxportal'
import styled from "styled-components";
import indexPng from './index.png'

const AppFrame = styled.div`
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  box-sizing: border-box;
`

const CodeSpan = styled.div`
  background-color: #333;
  padding: 10px;
  color: #FFF;
  font-size: 18px;
  margin: 20px;
`

function App() {
  const { connect, confluxState, openAccountModal, closeAccountModal } = useConfluxPortal()

  useEffect(() => {
    if (confluxState.isAvailable && !confluxState.isConnected) {
      (async () => {
        try {
          const accounts = await connect(window.conflux)
          console.log(accounts)
        } catch (error) {
          console.log(error)
        }
      })()
    }
  }, [confluxState.isAvailable])
  return (
    <AppFrame>
      <CodeSpan>
        {`import {useConfluxPortal, AccoutsModal} from 'use-confluxportal'`}
      </CodeSpan>
      <CodeSpan>
        {`const {connect, confluxState, openAccountModal, closeAccountModal} = useConfluxPortal()`}
      </CodeSpan>
      <img src={indexPng} />
      <h2>React Hooks</h2>
      <button onClick={openAccountModal}>
        {
          confluxState.account ? confluxState.account : '连接钱包'
        }
      </button>

      <a href={'https://github.com/liwei-web/use-confluxportal/tree/main/example'}>source code</a>
      <AccoutsModal />
    </AppFrame>
  );
}

export default App;
